import firebase from 'firebase';

const initFirebase = async () => {
  try {
    firebase.initializeApp({
      apiKey: 'AIzaSyD6M4Qgp4QzRf1PMYCsOTpDuVYSm0GAFbU',
      appId: '1:1081950831698:web:0ff6ad89d040347363cb8b',
      projectId: 'wetaxi-marketplace',
      messagingSenderId: '1081950831698',
    });

    const messaging = firebase.messaging();
    console.log(process.env);
    const vapidKey =
      'BA8mZzyT0O61OHvcdGEkR5WjNjDlnFtUmM5is00EUtF00swwDEDK5efr5Da5tFV4IXoCIpHgfEVADoeNfn6uzTo';
    if ('serviceWorker' in navigator) {
      navigator.serviceWorker
        .register('./firebase-messaging-sw.js')
        .then(function (registration) {
          console.log('Registration successful, scope is:', registration.scope);
          console.log(registration);
          messaging
            .getToken({
              vapidKey,
              serviceWorkerRegistration: registration,
            })
            .then(currentToken => {
              if (currentToken) {
                console.log('current token for client: ', currentToken);

                // Track the token -> client mapping, by sending to backend server
                // show on the UI that permission is secured
              } else {
                console.log(
                  'No registration token available. Request permission to generate one.',
                );

                // shows on the UI that permission is required
              }
            })
            .catch(err => {
              console.log('An error occurred while retrieving token. ', err);
              // catch error while creating client token
            });
        })
        .catch(function (err) {
          console.log('Service worker registration failed, error:', err);
        });
    }

    if (firebase.messaging.isSupported()) {
      const req = await Notification.requestPermission();
      console.log(req);
      const token = await messaging.getToken({ vapidKey });
      const oldToken = localStorage.getItem('gtoken');

      if (oldToken !== token || oldToken === null) {
        localStorage.setItem('gtoken', token);
      } else {
        console.log('token setup error');
      }
      messaging.onMessage(payload => {
        console.log(payload);
        const { title, body, icon, clickAction } = payload.data;
        new Notification(title, {
          body,
          icon,
        }).onclick = event => {
          event.preventDefault();
          window.location = clickAction;
        };
      });
    } else {
      localStorage.setItem('gtoken', false);
    }
  } catch (error) {
    console.log('Firebase notifications disabled', error);
  }
};

export default initFirebase;

